<template>
	<view class="login">
		<!-- 登录框 -->
		<view>
			<view class="login-img">
				<view class="owl-login" :class="hideEyes?'password':''">
					<view class="hand"></view>
					<view class="hand hand-r"></view>
					<view class="arms">
						<view class="arm"></view>
						<view class="arm arm-r"></view>
					</view>
				</view>
			</view>
			<view class="login-form">
				<form @submit="formSubmit">
					<view class="form-input">
						<input type="text" value="" placeholder="用户名" />
					</view>
					<view class="form-input">
						<input type="password" value="" placeholder="密码" @focus="passwordF_B" @blur="passwordF_B" />
					</view>
					<button type="primary" form-type="submit">登录</button>
				</form>
			</view>
		</view>
		<!-- 三方登录 -->
		<view class="login-footer">
			<view class="footer-tip flex">其他登录方式</view>
			<view class="footer-other flex">
				<button class="other-list"  open-type="getUserInfo" @getuserinfo="loginqq">
					<image class="img" src="../../static/img/login/ic-QQ@2x.png" mode="aspectFill"></image>
				</button>
				<button class="other-list" open-type="getUserInfo" @getuserinfo="loginWeixin">
					<image class="img" src="../../static/img/login/ic-weixin@2x.png" mode="aspectFill"></image>
				</button>
			</view>
		</view>
	</view>
</template>

<script>
	import { createNamespacedHelpers, mapGetters } from "vuex";
	const { mapMutations } = createNamespacedHelpers("user");
	
	export default {
		data() {
			return {
				phone: "",
				code: '',
				key: '',
				getCodeText: '获取验证码',
				getCodeBtnColor: "#ffffff",
				getCodeisWaiting: false,
				hideEyes: false
			}
		},
		computed: {
			...mapGetters(["currentPage"])
		},
		methods: {
			...mapMutations(["setUserInfo", "setToken", "setIsLogin"]),
			// 聚焦密码框，加载动画
			passwordF_B() {
				this.hideEyes = !this.hideEyes;
			},
			formSubmit(e) {
				console.log(e.detail.value);
			},
			Timer() {},
			getCode() {
				let _this = this;
				uni.hideKeyboard()
				if (_this.getCodeisWaiting) {
					return;
				}
				if (!(/^1(3|4|5|6|7|8|9)\d{9}$/.test(_this.phone))) {
					uni.showToast({
						title: '请填写正确手机号码',
						icon: "none"
					});
					return false;
				}
				_this.getCodeText = "发送中..."
				_this.getCodeisWaiting = true;
				_this.getCodeBtnColor = "rgba(255,255,255,0.5)"

				uni.request({
					url: _this.websiteUrl + '/sms/notification-sms/codes',
					data: {
						'phone': _this.phone
					},
					method: 'POST',
					header: {
						'Content-Type': 'application/x-www-form-urlencoded',
						//自定义请求头信息
					},
					success: (res) => {
						_this.key = res.data.data.key;
						//TODO 开发模式
						_this.code = res.data.data.code;
					}
				});
				//示例用定时器模拟请求效果
				setTimeout(() => {
					//uni.showToast({title: '验证码已发送',icon:"none"});
					_this.setTimer();
				}, 1000)
			},
			setTimer() {
				let holdTime = 59;
				
				this.getCodeText = "重新获取(60)"
				this.Timer = setInterval(() => {
					if (holdTime <= 0) {
						this.getCodeisWaiting = false;
						this.getCodeBtnColor = "#ffffff";
						this.getCodeText = "获取验证码"
						clearInterval(this.Timer);
						return;
					}
					this.getCodeText = "重新获取(" + holdTime + ")"
					holdTime--;
				}, 1000)
			},
			doLogin() {
				uni.hideKeyboard()
				//模板示例部分验证规则
				// if(!(/^1(3|4|5|6|7|8|9)\d{9}$/.test(this.phone))){ 
				// 	uni.showToast({title: '请填写正确手机号码',icon:"none"});
				// 	return false; 
				// } 

				uni.request({
					url: this.websiteUrl + '/token/sys/login-sms',
					data: {
						'key': this.key,
						'code': this.code,
						'phone': this.phone
					},
					method: 'POST',
					header: {
						'Content-Type': 'application/x-www-form-urlencoded',
					},
					success: (res) => {
						if (res.data.code == 200) {
							this.login(true, res.data.data, function() {
								this.getRongyToken();
							});
						} else {
							uni.showToast({
								title: '验证码不正确',
								icon: "none"
							});
							return false;
						}
					}
				});
			},
			//QQ登录
			loginqq() {
				console.log("qq登录");
				let _this = this;
				// uni.login({
				// 	provider: 'qq',
				// 	success: function(loginRes) {
				// 		// 获取用户信息
				// 		uni.getUserInfo({
				// 			provider: 'qq',
				// 			success: function(infoRes) {
				// 				_this.otherLogin(loginRes, infoRes, 'qq')
				// 			}
				// 		});
				// 	}
				// });
			},
			//微信登录
			loginWeixin() {
				uni.getUserInfo({
					provider: 'weixin',
					success: infoRes => {
						if (infoRes.errMsg === "getUserInfo:ok") {
							this.setUserInfo(infoRes.userInfo);
							this.setToken("imtokenADASDASDQADASDsdsdd");
							this.setIsLogin(true);
							uni.navigateBack()
							// uni.redirectTo({
							// 	url: this.currentPage,
							// 	fail: () => {
							// 		// 跳转失败兼容到tabbar
							// 		uni.switchTab({
							// 				url: this.currentPage
							// 		})
							// 	}
							// })
						}
					}
				});
			},
			//授权登录
			otherLogin(loginRes, infoRes, type) {
				let _this = this;
				let url;
				let pram = {};
				// _this.loginRes=JSON.stringify(loginRes).toString();
				// _this.infoRes=JSON.stringify(infoRes).toString();
				switch (type) {
					case 'qq':
						url = '/token/sys/login-qq';
						pram = {
							'openid': loginRes.authResult.openid,
							'nickname': infoRes.userInfo.nickname,
							'gender': infoRes.userInfo.gender,
							'province': infoRes.userInfo.province,
							'city': infoRes.userInfo.city,
							'figureurl': infoRes.userInfo.figureurl_qq
						}
						break;
					case 'wx':
						url = '/token/sys/login-wechat';
						pram = {
							'openid': loginRes.authResult.openid,
							'nickname': infoRes.userInfo.nickName,
							'sex': infoRes.userInfo.gender,
							'province': infoRes.userInfo.province,
							'city': infoRes.userInfo.city,
							'country': infoRes.userInfo.country,
							'headimgurl': infoRes.userInfo.avatarUrl,
							'unionid': loginRes.authResult.unionid
						}
						break;
					default:
				}
				uni.request({
					url: _this.websiteUrl + url,
					data: pram,
					method: 'POST',
					header: {
						'Content-Type': 'application/x-www-form-urlencoded',
					},
					success: (res) => {
						if (res.data.code == 200) {
							//_this.testData=JSON.stringify(res.data.data).toString();;
							_this.login(true, res.data.data, function() {
								_this.getRongyToken();
							});

						}
					}
				});
			},
		}
	}
</script>

<style scoped>
	.login-form {
		margin: -18rpx 20rpx 0 20rpx;
		height: 464rpx;
		background: #007AFF;
		border-radius: 20rpx;
		background-color: #EEEEEE;
		box-shadow: 0 4rpx 20rpx #9B9B9B;
		padding: 40rpx;
	}

	.form-input input {
		background: #ffffff;
		border-radius: 10rpx;
		height: 80rpx;
		margin: 40rpx 0;
		padding: 0 20rpx;
	}

	.login-img {
		display: flex;
		justify-content: center;
		align-items: center;
		height: 216rpx;
		margin-top: 100rpx;
	}

	.owl-login {
		width: 422rpx;
		height: 216rpx;
		background-image: url('data:image/png;base64,');
		background-repeat: no-repeat;
		background-size: 100%;
		position: absolute;
	}

	.owl-login .hand {
		width: 68rpx;
		height: 68rpx;
		-webkit-border-radius: 80rpx;
		border-radius: 80rpx;
		background-color: #472d20;
		transform: scaleY(0.6);
		transition: 0.3s ease-out;
		position: absolute;
		left: 28rpx;
		bottom: -16rpx;
	}

	.owl-login .hand.hand-r {
		left: 340rpx;
	}

	.owl-login .arms {
		top: 116rpx;
		position: absolute;
		width: 100%;
		height: 82rpx;
		overflow: hidden;
	}

	.owl-login .arms .arm {
		width: 80rpx;
		height: 130rpx;
		position: absolute;
		left: 40rpx;
		top: 80rpx;
		background-image: url("data:image/png;base64,");
		background-size: 100%;
		transition: 0.3s ease-out;
		transform: rotate(-20deg);
	}

	.owl-login .arms .arm.arm-r {
		transform: rotate(20deg) scaleX(-1);
		left: 316px;
	}

	.owl-login.password .hand {
		transform: translateX(84rpx) translateY(-30rpx) scale(0.7);
	}

	.owl-login.password .hand.hand-r {
		transform: translateX(-84rpx) translateY(-30rpx) scale(0.7);
	}

	.owl-login.password .arms .arm {
		transform: translateY(-80rpx) translateX(80rpx);
	}

	.owl-login.password .arms .arm.arm-r {
		transform: translateY(-80rpx) translateX(-80rpx) scaleX(-1);
	}
	
	.login-footer {
		position: fixed;
		bottom: 0;
		width: 100%;
		padding: 0 40rpx;
	}
	
	.footer-tip {
		align-items: center;
		font-size: 24rpx;
		color: #999999;
		text-align: center;
	}
	
	.footer-tip::before {
		flex: 1;
		content: '';
		height: 2rpx;
		background: #D0D0D0;
		margin-right: 30rpx;
	}
	
	.footer-tip::after {
		margin-left: 30rpx;
		flex: 1;
		content: '';
		height: 2rpx;
		background: #D0D0D0;
	}
	
	.footer-other {
		padding: 40rpx 0 80rpx;
		justify-content: center;
	}
	
	.other-list {
		width: 80rpx;
		height: 80rpx;
		margin: 0 75rpx;
		padding: 0;
		border-radius: 50%;
		background-color: #fff;
	}
	
	.img {
		width: 100%;
		height: 100%;
	}
</style>
